.Navbar {
  position: fixed;
  top: 0;
  /* Stick it to the top */
  max-height: 70px;
  width: 100%;
  position: relative;
  display: grid;
  grid-template-areas: "logo nav";
}
@media (max-width: 768px) {
  .Navbar {
    grid-template-areas: "logo burger" "nav nav";
    justify-items: right;
  }
}
.Navbar a {
  color: #f53b57;
  padding: 18px;
  border-bottom: 5px solid transparent;
}
.Navbar a:hover {
  color: #f53b57;
  border-bottom: 5px solid #f53b57;
}
.Navbar a.active {
  color: #f53b57;
  border-bottom: 5px solid #f53b57;
}
