.Logo {
  grid-area: logo;
  height: 70px;
  width: 70px;
}
.Logo:hover {
  border: 0 !important;
}
.Logo img {
  height: 100%;
  position: absolute;
  top: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.Logo img:hover {
  opacity: 0.8;
}
@media (max-width: 768px) {
  .Logo {
    margin-right: -70px;
  }
}
