.Guests {
  background: #f7e9e8;
  padding: 100px 0;
  min-height: 800px;
}
.Guests__content {
  width: 768px;
  font-size: 20px;
  line-height: 2em;
  text-align: center;
  margin: 0 auto;
  font-family: 'Pacifico', cursive;
}
@media (max-width: 1200px) {
  .Guests__content {
    width: 100%;
  }
}
.Guests__pose {
  display: block;
  margin: 0 auto;
  width: 310px;
}
